  components: {
    AppHeaderDropdown,
  },

  computed: {
    ...mapGetters({
      userLogin: 'authentication/getUserLogin',
    }),
  },

  methods: {
    ...mapActions({
      logout: 'authentication/logout',
    }),
  },
};
</script>

<template>
  <AppHeaderDropdown
    right
    no-caret
  >
    <template slot="header">
      {{ userLogin }}
    </template>
    <template slot="dropdown">
      <b-dropdown-item
        href="#/"
        @click="logout()"
      >
        <i class="fa fa-lock" />Выйти
      </b-dropdown-item>
    </template>
  </AppHeaderDropdown>
</template>
